import React, { useEffect, useState, useCallback } from "react";
import { Switch, Route } from "react-router-dom";
import API from "../../utils/Api";
import { loadShips, loadTransportTypes } from "../../utils/utils_options";
import { useAuth } from "../../context/auth";
import { ThemeContext } from "../../context/theme";
import Icon from "../../assets/icon.png";

import ChangePasswordModal from "../UI/Modals/ChangePassword";
import ChangeProfileModal from "../UI/Modals/ChangeProfile";
import Report from "../UI/Modals/Report";

import {
  Drawer,
  AppBar,
  Toolbar,
  List,
  Typography,
  IconButton,
  ListItem,
  ListItemIcon,
  ListItemText,
  Collapse,
} from "@material-ui/core";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faCog,
  faFileExcel,
  faKey,
  faPrint,
  faUser,
} from "@fortawesome/free-solid-svg-icons";

import MenuIcon from "@material-ui/icons/Menu";
import AccountCircleIcon from "@material-ui/icons/AccountCircle";
import HomeIcon from "@material-ui/icons/Home";
import ExitToAppIcon from "@material-ui/icons/ExitToApp";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import ExpandLessIcon from "@material-ui/icons/ExpandLess";

import clsx from "clsx";
import { makeStyles } from "@material-ui/core/styles";
import "./dashboard.css";
import Ships from "./Ships/Ships";
import DashboardTab from "../UI/Tabs/DashboardTab";
import { ProfileContext } from "../../context/profile";
import { OptionsContext } from "../../context/options";

const drawerWidth = 220;

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
  },
  appBar: {
    transition: theme.transitions.create(["margin", "width"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    // backgroundColor: '#F8B828'
    backgroundColor: "#1a1751",
  },
  appBarShift: {
    width: `calc(100% - ${drawerWidth}px)`,
    marginLeft: drawerWidth,
    transition: theme.transitions.create(["margin", "width"], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  hide: {
    display: "none",
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
    boxShadow: "1px 0px 10px #000",
  },
  drawerHeader: {
    display: "flex",
    // height: '170px',
    alignItems: "center",
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
    // justifyContent: 'flex-end',
    backgroundColor: "#FFFFFF",
    color: "#FFFFFF",
    margin: "0px",
    padding: "0px",
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(4),
    transition: theme.transitions.create("margin", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    marginLeft: -drawerWidth,
    justifyContent: "center"
  },
  contentShift: {
    transition: theme.transitions.create("margin", {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
    marginLeft: 0,
  },
  large: {
    width: theme.spacing(10),
    height: theme.spacing(10),
  },
  nested: {
    paddingLeft: theme.spacing(4),
  },
}));

export default function Dashboard(props) {
  const auth_context = useAuth();
  const token = auth_context.authTokens;
  const user = auth_context.authTokens.user;
  const classes = useStyles();
  const [drawerStatus, setDrawerStatus] = useState(true);
  const [userName, setUsername] = useState(user);
  const [profileDetails, setProfileDetails] = useState({});
  const [isManageCollapse, setIsManageCollapse] = useState(false);
  const [isReportCollapse, setIsReportCollapse] = useState(false);
  const [modalMarginXL, setModalMarginXL] = useState("20%");
  const [modalMarginLG, setModalMarginLG] = useState("30%");

  const [shipOptions, setShipOptions] = useState({});
  const [transportOptions, setTransportOptions] = useState({});

  const [changePasswordModal, setVisiblechangePasswordModal] = useState(false);
  const toggleChangePasswordModal = () =>
    setVisiblechangePasswordModal(!changePasswordModal);
  const [changeProfileModal, setChangeProfileModal] = useState(false);
  const toggleChangeProfileModal = () =>
    setChangeProfileModal(!changeProfileModal);
  const [passengerManifestReportModal, setPassengerManifestReportModal] =
    useState(false);
  const togglePassengerManifestReport = () =>
    setPassengerManifestReportModal(!passengerManifestReportModal);

  const handleDrawer = () => {
    setDrawerStatus(!drawerStatus);
    if (drawerStatus) {
      setModalMarginXL("13%");
      setModalMarginLG("25%");
    } else {
      setModalMarginXL("20%");
      setModalMarginLG("30%");
    }
  };

  const handleManageCollapse = () => {
    setIsManageCollapse(!isManageCollapse);
  };

  const handleReportCollapse = () => {
    setIsReportCollapse(!isReportCollapse);
  };

  const handleNavigation = (stringPath = "/") => {
    switch (stringPath) {
      case "/":
        props.history.push("/");
        break;
      case "ships":
        props.history.push("/ships");
        break;
      default:
        break;
    }
  };

  const handleLogout = () => {
    sessionStorage.removeItem("transporttokens");
    window.location.reload();
  };

  const reportHandler = (opt) => {
    let reportOptions = {
      manifest: 1,
    };

    switch (opt) {
      case reportOptions.manifest:
        togglePassengerManifestReport();
        break;
      default:
        break;
    }
  };

  const getProfileDetails = () => {
    API.get(`auth/carrier/me`, {
      headers: {
        Authorization: "bearer " + token.access_token,
      },
    }).then((result) => {
      setProfileDetails(result.data);
    });
  };

  const getShipsOptions = useCallback(() => {
    loadShips(token).then((result) => {
      let ships = [];

      if (result.data.length > 0) {
        for (let s in result.data) {
          let tempShipArr = {
            value: result.data[s].id,
            label: result.data[s].name,
          };
          ships.push(tempShipArr);
        }
      }

      setShipOptions(ships);
    });
  }, []);

  const getTransportOptions = useCallback(() => {
    loadTransportTypes(token).then((result) => {
      let category = [];

      if (result.data.length > 0) {
        for (let s in result.data) {
          let tempCategory = {
            value: result.data[s].id,
            label: result.data[s].name,
          };
          category.push(tempCategory);
        }
      }

      setTransportOptions(category);

    });
  }, []);
  
  useEffect(() => {
    getProfileDetails();
    getShipsOptions();
    getTransportOptions();
    return () => {};
  }, []);

  return (
    <ThemeContext.Provider value={{ modalMarginXL, modalMarginLG }}>
      <ProfileContext.Provider value={{ setUsername, profileDetails, getProfileDetails }}>
        <OptionsContext.Provider value={{ getShipsOptions, shipOptions, getTransportOptions, transportOptions }}>
          <div className={classes.root}>
            <AppBar
              position="fixed"
              className={clsx(classes.appBar, {
                [classes.appBarShift]: drawerStatus,
              })}
            >
              <Toolbar>
                <IconButton
                  color="inherit"
                  aria-label="open drawer"
                  onClick={handleDrawer}
                  edge="start"
                  className={clsx(classes.menuButton)}
                  style={{ outline: "none" }}
                >
                  <MenuIcon />
                </IconButton>
                <Typography variant="h6" noWrap>
                  <div className="float-left">
                    <img src={Icon} width="33" height="35" alt="EMS Logo" />
                  </div>
                  <div className="float-left">
                    <ul
                      className="p-0 m-0 mt-2 ml-1"
                      style={{ listStyle: "none", lineHeight: "0.9rem" }}
                    >
                      <li style={{ fontSize: "1.5rem" }}>PEMS</li>
                      <li>
                        <small style={{ fontSize: "0.8rem" }}>
                          Passenger e-Manifest System
                        </small>
                      </li>
                    </ul>
                  </div>
                </Typography>
              </Toolbar>
            </AppBar>
            <Drawer
              className={classes.drawer}
              variant="persistent"
              anchor="left"
              open={drawerStatus}
              classes={{
                paper: classes.drawerPaper,
              }}
            >
              <div className={classes.drawerHeader}>
                <div
                  className="container-fluid text-left mb-2"
                  style={{
                    color: "#1a1751",
                    minHeight: "125px",
                    height: "125px",
                  }}
                >
                  <div className="text-center mt-4 pb-2 border-bottom">
                    <AccountCircleIcon
                      style={{ width: "45px", height: "50px" }}
                    />
                    <ul
                      className="m-0 p-0 mt-2"
                      style={{ listStyle: "none", lineHeight: "1em" }}
                    >
                      <li className="border p-1 bg-primary text-white">
                        <strong>{userName.toUpperCase()}</strong>
                      </li>
                      <li className="mt-2">Operator</li>
                    </ul>
                  </div>
                </div>
              </div>
              <List style={{ display: "flex", flexDirection: "column" }}>
                <ListItem
                  className="mt-4"
                  button
                  onClick={() => handleNavigation("/")}
                >
                  <ListItemIcon>
                    <HomeIcon />
                  </ListItemIcon>
                  <ListItemText primary={"Dashboard"} />
                </ListItem>
                <ListItem button onClick={handleReportCollapse}>
                  <ListItemIcon>
                    <FontAwesomeIcon icon={faPrint} size="lg" />
                  </ListItemIcon>
                  <ListItemText primary="Reports" />
                  {isReportCollapse ? <ExpandLessIcon /> : <ExpandMoreIcon />}
                </ListItem>
                <Collapse in={isReportCollapse} timeout="auto" unmountOnExit>
                  <List component="div" disablePadding>
                    <ListItem
                      button
                      onClick={() => reportHandler(1)}
                      className={classes.nested}
                    >
                      <ListItemIcon>
                        <FontAwesomeIcon icon={faFileExcel} />
                      </ListItemIcon>
                      <ListItemText primary={"Manifest"} />
                    </ListItem>
                  </List>
                </Collapse>
                <ListItem button onClick={handleManageCollapse}>
                  <ListItemIcon>
                    <FontAwesomeIcon icon={faCog} size="lg" />
                  </ListItemIcon>
                  <ListItemText primary="Manage" />
                  {isManageCollapse ? <ExpandLessIcon /> : <ExpandMoreIcon />}
                </ListItem>
                <Collapse in={isManageCollapse} timeout="auto" unmountOnExit>
                  <List component="div" disablePadding>
                    <ListItem
                      button
                      onClick={toggleChangePasswordModal}
                      className={classes.nested}
                    >
                      <ListItemIcon>
                        <FontAwesomeIcon icon={faKey} />
                      </ListItemIcon>
                      <ListItemText primary={"My Password"} />
                    </ListItem>
                  </List>
                  <List component="div" disablePadding>
                    <ListItem
                      button
                      onClick={toggleChangeProfileModal}
                      className={classes.nested}
                    >
                      <ListItemIcon>
                        <FontAwesomeIcon icon={faUser} />
                      </ListItemIcon>
                      <ListItemText primary={"My Profile"} />
                    </ListItem>
                  </List>
                </Collapse>
                <ListItem button onClick={() => handleLogout()}>
                  <ListItemIcon>
                    <ExitToAppIcon />
                  </ListItemIcon>
                  <ListItemText primary={"Logout"} />
                </ListItem>
              </List>
            </Drawer>
            <main
              className={clsx(classes.content, {
                [classes.contentShift]: drawerStatus,
              })}
            >
              <Switch>
                <Route exact path="/">
                  <DashboardTab />
                </Route>
                <Route exact path="/ships">
                  <Ships />
                </Route>
              </Switch>

              {changePasswordModal && (
                <ChangePasswordModal
                  isOpen={changePasswordModal}
                  toggle={toggleChangePasswordModal}
                />
              )}

              {changeProfileModal && (
                <ChangeProfileModal
                  isOpen={changeProfileModal}
                  toggle={toggleChangeProfileModal}
                />
              )}

              {passengerManifestReportModal && (
                <Report
                  isOpen={passengerManifestReportModal}
                  toggle={togglePassengerManifestReport}
                />
              )}
            </main>
          </div>
        </OptionsContext.Provider>
      </ProfileContext.Provider>
    </ThemeContext.Provider>
  );
}
