import React, { useState } from "react";
import { Redirect } from "react-router-dom";
import { useAuth } from "../../context/auth";
import TextField from "@material-ui/core/TextField";
import InputAdornment from "@material-ui/core/InputAdornment";
import IconButton from "@material-ui/core/IconButton";
import Visibility from "@material-ui/icons/Visibility";
import VisibilityOff from "@material-ui/icons/VisibilityOff";
import Button from "@material-ui/core/Button";
import { Container, Row, Col, Card, Form, Spinner } from "reactstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faAnchor,
  faCheck,
  faSignInAlt,
} from "@fortawesome/free-solid-svg-icons";
import "../Register/register.css";
import Logo from "../../assets/logo.jpg";
import MARINA from "../../assets/marina.png";
import Error from "../UI/Error/Error";
import API from "../../utils/Api";
import Swal from "sweetalert2";

const Register = (props) => {
  const [isProcessing, setProcessing] = useState(false);
  const [isError, setIsError] = useState(false);
  const [errorMessage, setErrorMessage] = useState(
    "Oops! sorry something went wrong..."
  );
  const authTokens = useAuth().authTokens;
  const [isShowPass, setIsShowPass] = useState(false);
  const toggleShowPassword = () => setIsShowPass(!isShowPass);
  const [isShowRePass, setIsShowRePass] = useState(false);
  const toggleShowRePassword = () => setIsShowRePass(!isShowRePass);

  const isAuthenticatedHandler = () => {
    const token = authTokens;

    if (token === null) {
      return false;
    } else {
      if (token.expires_in < Date.now()) {
        return true;
      }
    }
    return false;
  };

  if (isAuthenticatedHandler()) {
    return <Redirect to="/" />;
  }

  const RegisterHandler = (e) => {
    e.preventDefault();
    const company = document.querySelector("input[name=company]").value;
    const mobile = document.querySelector("input[name=mobile]").value;
    const email = document.querySelector("input[name=email]").value;
    const address = document.querySelector("input[name=address]").value;
    const username = document.querySelector("input[name=username]").value;
    const password = document.querySelector("input[name=password]").value;
    const repassword = document.querySelector("input[name=repassword]").value;

    if (username !== "" && password !== "") {
      if (password !== repassword) {
        setErrorMessage("Password doesn't match");
        setIsError(true);
        return null;
      }

      setProcessing(true);
      let postData = {
        carrier_name: company,
        carrier_contact: mobile,
        carrier_address: address,
        carrier_email: email,
        username: username,
        password: password,
      };

      API.post("account/register", postData)
        .then((result) => {
          setProcessing(false);
          if (result.status === 200) {
            if (result.data.error === 0) {
              setErrorMessage(result.data.message);
              setIsError(true);
            } else {
              let res = Swal.fire(
                "Transaction Completed!",
                "Account Successfully Registered!",
                "success"
              );

              res.then(() => {
                props.history.push("/");
              });
            }
          } else if (result.status === 254) {
            setErrorMessage(result.data.message);
            setIsError(true);
          } else {
            setIsError(true);
          }
        })
        .catch((e) => {
          setProcessing(false);
          setIsError(true);
        });
    } else {
      setErrorMessage("Please enter a username and password");
      setIsError(true);
    }
  };

  return (
    <Container className="rounded" style={{ width: "762px", marginTop: "2%" }}>
      <Card
        className="bg-primary p-0 m-0 rounded"
        style={{ border: "none", opacity: "0.88" }}
      >
        <Row className="p-0 m-0">
          <Col xs={12} className="p-0 m-0">
            <Card className="p-0 m-0" style={{ borderRadius: "0px" }}>
              <Col xs={12} className="mt-4 text-center">
                <img
                  className="mt-1"
                  src={MARINA}
                  style={{ width: "4rem", height: "4.5rem" }}
                  alt="MARINA Logo"
                />
                <img
                  className="mt-1"
                  src={Logo}
                  style={{ width: "5rem", height: "6rem" }}
                  alt="Passenger eTracer Logo"
                />
              </Col>
              <Col xs={12} className="mt-1 text-center">
                <h5>
                  <FontAwesomeIcon className="pr-1" icon={faAnchor} /> Register
                  New Company
                </h5>
              </Col>
              <Col xs={{ size: 10, offset: 1 }}>
                <Form onSubmit={(e) => RegisterHandler(e)} autoComplete="off">
                  <Col xs={12} className="pl-4 pr-4 mb-2 text-center">
                    {isError && <Error>{errorMessage}</Error>}
                  </Col>
                  <Col xs={12} className="pl-4 pr-4 mb-2">
                    <TextField
                      name="company"
                      placeholder="Company Name"
                      label="Company Name"
                      fullWidth
                      required
                    />
                  </Col>
                  <Col xs={12} className="pl-4 pr-4 mb-2">
                    <TextField
                      name="mobile"
                      placeholder="Contact Number"
                      label="Contact Number"
                      fullWidth
                      required
                    />
                  </Col>
                  <Col xs={12} className="pl-4 pr-4 mb-2">
                    <TextField
                      name="email"
                      placeholder="Email"
                      label="Email"
                      fullWidth
                      required
                    />
                  </Col>
                  <Col xs={12} className="pl-4 pr-4 mb-2">
                    <TextField
                      name="address"
                      placeholder="Address"
                      label="Address"
                      fullWidth
                      required
                    />
                  </Col>
                  <Col xs={12} className="pl-4 pr-4 mb-2">
                    <TextField
                      name="username"
                      placeholder="Username"
                      label="Username"
                      fullWidth
                      required
                    />
                  </Col>
                  <Col xs={12} className="pl-4 pr-4 mb-2">
                    <TextField
                      type={isShowPass ? "text" : "password"}
                      name="password"
                      placeholder="Password"
                      label="Password"
                      fullWidth
                      required
                      InputProps={{
                        endAdornment: (
                          <InputAdornment position="end">
                            <IconButton onClick={toggleShowPassword}>
                              {isShowPass ? <Visibility /> : <VisibilityOff />}
                            </IconButton>
                          </InputAdornment>
                        ),
                      }}
                    />
                  </Col>
                  <Col xs={12} className="pl-4 pr-4 mb-2">
                    <TextField
                      type={isShowRePass ? "text" : "password"}
                      name="repassword"
                      placeholder="Re-type Password"
                      label="Re-type Password"
                      fullWidth
                      required
                      InputProps={{
                        endAdornment: (
                          <InputAdornment position="end">
                            <IconButton onClick={toggleShowRePassword}>
                              {isShowRePass ? (
                                <Visibility />
                              ) : (
                                <VisibilityOff />
                              )}
                            </IconButton>
                          </InputAdornment>
                        ),
                      }}
                    />
                  </Col>
                  <Col xs={12} className="pl-4 pr-4 mt-3 mb-2">
                    {isProcessing ? (
                      <div
                        className="text-center mt-1"
                        style={{ minHeight: "100px" }}
                      >
                        <Spinner
                          className="mt-4"
                          style={{ width: "2rem", height: "2rem" }}
                          color="primary"
                        />
                        <br />
                        <span className="mt-1">Processing...</span>
                      </div>
                    ) : (
                      <div>
                        <Button
                          type="submit"
                          variant="contained"
                          color="primary"
                          className="pt-1"
                          disabled={isProcessing}
                          fullWidth
                        >
                          <span>Register</span>
                          <FontAwesomeIcon className="pl-1" icon={faCheck} />
                        </Button>
                        <div className="text-center m-0 p-0 mt-2">
                          <span>or</span>
                        </div>
                        <div className="text-center">
                          <a
                            href="login"
                            variant="contained"
                            color="primary"
                            className="pt-1 btn"
                            disabled={isProcessing}
                          >
                            <span>LOGIN HERE</span>
                            <FontAwesomeIcon
                              className="pl-1"
                              icon={faSignInAlt}
                            />
                          </a>
                        </div>
                      </div>
                    )}
                  </Col>
                </Form>
              </Col>
            </Card>
          </Col>
        </Row>
      </Card>
    </Container>
  );
};

export default Register;
